import React from "react";
import "./App.css";

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      selectedMainRadio: "single",
      namefield: "",
      couponfield: "",
      discountType: "percentage",
      discount: "",
      amount: "",
      redeemChecked: false,
      expiryChecked: false,
      coursesChecked: false,
      redeemFrequency_field: "",
      expiryDate_field: "",
      specificCourses_field: "",
    };
  }

  submitForm = (e) => {
    e.preventDefault();
    console.log(this.state);
  };

  render() {
    let radio1 =
      this.state.selectedMainRadio === "single"
        ? "radio1 selected-radio"
        : "radio1";
    let radio2 =
      this.state.selectedMainRadio === "bulk"
        ? "radio2 selected-radio"
        : "radio2";
    return (
      <div className="form-container">
        <div className="head">
          <span>Add Coupon Code</span>
        </div>
        <form onSubmit={this.submitForm}>
          <div className="coupon-type">
            <div className={radio1}>
              <input
                checked={this.state.selectedMainRadio === "single"}
                value="single"
                onChange={(e) => {
                  this.setState({ selectedMainRadio: e.target.value });
                  // console.log(this.state);
                }}
                id="single"
                type="radio"
                name="coupon-type"
              />
              <label>Single Coupon Code</label>
            </div>
            <div className={radio2}>
              <input
                value="bulk"
                checked={this.state.selectedMainRadio === "bulk"}
                onChange={(e) => {
                  this.setState({ selectedMainRadio: e.target.value });
                  // console.log(this.state);
                }}
                id="bulk"
                type="radio"
                name="coupon-type"
              />
              <label>Bulk Coupon Codes</label>
            </div>
          </div>

          <div className="main-fields">
            <div className="name-field">
              <label className="txt">Enter Name</label>
              <input
                value={this.state.namefield}
                onChange={(e) => {
                  this.setState({ namefield: e.target.value });
                }}
                type="text"
                placeholder="Input text"
              />
            </div>
            <div className="coupon-field">
              <label className="txt">Enter Coupon Code</label>
              <input
                value={this.state.couponfield}
                onChange={(e) => {
                  this.setState({ couponfield: e.target.value });
                }}
                type="text"
                placeholder="Input text"
              />
            </div>
          </div>

          <div className="discount-type-wrapper">
            <label className="txt">Type of Discount</label>
            <div className="radio-wrapper">
              <div className="radio-per">
                <div className="label-per">
                  <input
                    checked={this.state.discountType === "percentage"}
                    value="percentage"
                    onChange={(e) => {
                      this.setState({ discountType: e.target.value });
                    }}
                    id="percentage"
                    type="radio"
                    name="discount"
                  />
                  <label className="txt">Percentage discount</label>
                </div>

                {this.state.discountType === "percentage" && (
                  <input
                    value={this.state.discount}
                    onChange={(e) => {
                      this.setState({ discount: e.target.value });
                    }}
                    type="text"
                    placeholder="Enter discount"
                  />
                )}
              </div>

              <div className="radio-amt">
                <div className="label-amt">
                  <input
                    value="amount"
                    onChange={(e) => {
                      this.setState({ discountType: e.target.value });
                    }}
                    id="amount"
                    type="radio"
                    name="discount"
                  />
                  <label className="txt">Fixed amount discount</label>
                </div>

                {this.state.discountType === "amount" && (
                  <input
                    value={this.state.amount}
                    onChange={(e) => {
                      this.setState({ amount: e.target.value });
                    }}
                    type="text"
                    placeholder="Enter amount"
                  />
                )}
              </div>
            </div>
          </div>

          <div className="checkbox-wrapper">
            <div className="option1">
              <div className="checkbox1">
                <input
                  checked={this.state.redeemChecked}
                  onChange={() => {
                    this.setState({ redeemChecked: !this.state.redeemChecked });
                  }}
                  name="op1"
                  type="checkbox"
                />
                <label htmlFor="op1" className="txt">
                  Limit the number of times the code can be redeemed
                </label>
              </div>
              {this.state.redeemChecked && (
                <input
                  value={this.state.redeemFrequency_field}
                  onChange={(e) => {
                    this.setState({ redeemFrequency_field: e.target.value });
                  }}
                  name="checks"
                  type="number"
                  placeholder="Enter number"
                />
              )}
            </div>
            <div className="option2">
              <div className="checkbox2">
                <input
                  checked={this.state.expiryChecked}
                  onChange={() => {
                    this.setState({ expiryChecked: !this.state.expiryChecked });
                  }}
                  name="op2"
                  type="checkbox"
                />
                <label htmlFor="op2" className="txt">
                  Add an Expiry Date
                </label>
              </div>

              {this.state.expiryChecked && (
                <input
                  value={this.state.expiryDate_field}
                  onChange={(e) => {
                    this.setState({ expiryDate_field: e.target.value });
                  }}
                  name="checks"
                  type="text"
                  placeholder="Select date"
                />
              )}
            </div>
            <div className="option3">
              <div className="checkbox3">
                <input
                  checked={this.state.coursesChecked}
                  onChange={() => {
                    this.setState({
                      coursesChecked: !this.state.coursesChecked,
                    });
                  }}
                  name="op3"
                  type="checkbox"
                />
                <label htmlFor="op3" className="txt">
                  Limit to specific courses
                </label>
              </div>

              {this.state.coursesChecked && (
                <input
                  value={this.state.specificCourses_field}
                  onChange={(e) => {
                    this.setState({ specificCourses_field: e.target.value });
                  }}
                  name="checks"
                  type="text"
                  placeholder="Input text"
                />
              )}
            </div>
          </div>

          <div className="btns">
            <button>Cancel</button>
            <button className="submit-btn">Save</button>
          </div>
        </form>
      </div>
    );
  }
}

export default App;
